<template>
  <div class="login-page">
    <div class="login-card">
      <h1>로그인</h1>
      <p class="subtitle">User ID를 입력하면 Chat 페이지로 이동합니다.</p>

      <form @submit.prevent="handleLogin" class="login-form">
        <div class="form-group">
          <label for="system_id">System ID</label>
          <select id="system_id" v-model="systemId" class="form-input">
            <option value="cointutor">cointutor</option>
            <option value="drillquiz">drillquiz</option>
          </select>
        </div>
        <div class="form-group">
          <label for="user_id">User ID</label>
          <input
            id="user_id"
            v-model="userId"
            type="text"
            class="form-input"
            placeholder="사용자 ID (예: user123)"
            required
          >
        </div>
        <p v-if="error" class="error-msg">{{ error }}</p>
        <button type="submit" class="btn-submit" :disabled="loading">
          {{ loading ? '로그인 중...' : '로그인 후 Chat 열기' }}
        </button>
      </form>

      <p class="footer-note">
        API Key가 필요합니다. <code>VUE_APP_CHAT_GATEWAY_API_KEY</code>를 env에 설정하세요.
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data() {
    return {
      systemId: 'cointutor',
      userId: '',
      loading: false,
      error: ''
    }
  },
  methods: {
    async handleLogin() {
      this.error = ''
      this.loading = true
      const apiKey = process.env.VUE_APP_CHAT_GATEWAY_API_KEY || ''
      if (!apiKey) {
        this.error = 'VUE_APP_CHAT_GATEWAY_API_KEY가 설정되지 않았습니다. .env 또는 env-frontend에 추가하세요.'
        this.loading = false
        return
      }
      try {
        const url = `/v1/chat-token?system_id=${encodeURIComponent(this.systemId)}&user_id=${encodeURIComponent(this.userId.trim())}`
        const r = await fetch(url, {
          headers: { 'X-API-Key': apiKey }
        })
        if (!r.ok) {
          const text = await r.text()
          let msg = text
          try {
            const j = JSON.parse(text)
            if (j.detail) msg = typeof j.detail === 'string' ? j.detail : JSON.stringify(j.detail)
          } catch (_) {}
          throw new Error(msg || '토큰 발급 실패')
        }
        const data = await r.json()
        const token = data.token
        if (!token) throw new Error('토큰을 받지 못했습니다.')
        this.$router.push({ path: '/chat', query: { token } })
      } catch (e) {
        this.error = e.message || '로그인에 실패했습니다.'
      } finally {
        this.loading = false
      }
    }
  }
}
</script>

